/* Hide the default checkbox */
.checkbox-label{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.checkbox-label .input_bg{
  width: 80px;
  height: 50px;
  background: #F1F1F1;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.checkbox-label .input_bg input[type="checkbox"] {
  display: none;
  width: 30px;
}

/* Style the custom checkbox */
.checkbox-label .input_bg input[type="checkbox"] + label {
  position: relative;
  /* padding-left: 25px;   */
  cursor: pointer;
}

/* Create the custom checkbox icon */
.checkbox-label .input_bg input[type="checkbox"] + label::before {
  content: "";
  position: absolute;
  left: -20px;
  top: -8px;
  width: 36px;
  height: 29px;
  border-radius: 4px;
  background-color: #F1F1F1; /* Adjust the background color */
}

/* Style the custom checkbox when checked */
.checkbox-label .input_bg input[type="checkbox"]:checked + label::before {
  background-color: #F1F1F1; /* Adjust the background color for the checked state */
}

.checkbox-label .input_bg input[type="checkbox"]:checked + .checkbox-label .input_bg {
  background-color: red; /* Adjust the background color for the checked state */
}

/* Add a checkmark to the custom checkbox when checked */
.checkbox-label .input_bg input[type="checkbox"]:checked + label::before {
  content: "\2713";
  color: #029CAB;
  font-size: 23px;
  font-weight: bolder;
  line-height: 20px;
  text-align: center; 
}


